html {
    scroll-behavior: smooth;
}

body,
html {
    margin: 0;
    padding: 0;
    color: #555A64;
}

* {
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.wrapper {
    width: 75%;
    margin: 0 auto;
}

.margin-top-responsive {
    margin-top: 60px;
}

.contact-grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 50px;
}

.wrapper-full {
    width: 90%;
    margin: 0 auto;
}

@media all and (max-width: 641px) {
    .wrapper {
        width: 90%;
    }
}

@media all and (max-width: 441px) {
    .margin-top-responsive {
        margin-top: 100px;
    }
    .wrapper,
    .wrapper-full {
        width: 100%;
        padding: 0 16px;
    }
}

.img-responsive {
    max-width: 100%;
    display: block;
}

.img-fluid {
    width: 100%;
}

.d-grid {
    display: grid;
}

.d-flex {
    display: flex;
    display: -webkit-flex;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

button,
input,
select {
    -webkit-appearance: none;
    outline: none;
}

button,
.btn,
select {
    cursor: pointer;
}

a {
    text-decoration: none;
}

iframe {
    border: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
    margin: 0;
    padding: 0;
}

.p-relative {
    position: relative;
}

.p-absolute {
    position: absolute;
}

.p-fixed {
    position: fixed;
}

.p-sticky {
    position: -webkit-sticky;
    position: sticky;
}

body a:hover,
body button:hover {
    opacity: .8;
}

h1,
h2,
h3,
h4,
h5,
h6,
a {
    color: #2C3038;
}

h3.title-main {
    font-size: 40px;
    line-height: 45px;
    color: #2C3038;
    text-align: center;
    margin-bottom: 12px;
}

h3.title-white {
    color: #fff;
}

p.sub-title {
    color: #555A64;
    font-size: 17px;
    line-height: 25px;
    text-align: center;
    max-width: 600px;
    margin: 0 auto 50px;
}

a.button {
    border: none;
    color: #f05448;
    font-size: 16px;
    line-height: 25px;
    font-weight: 600;
    display: inline-block;
}

a.button-dark {
    color: #2C3038;
}

.buttonbg {
    border: none;
    font-weight: 600;
    display: inline-block;
    font-size: 17px;
    color: #fff;
    background: #f05448;
    text-align: center;
    padding: 12px 22px;
}

.buttonbg-dark {
    background: #2C3038;
}

@media screen and (max-width: 1080px) {
    h3.title-main {
        font-size: 35px;
        line-height: 40px;
    }
    p.sub-title {
        margin: 0 auto 45px;
    }
}

@media screen and (max-width: 800px) {
    h3.title-main {
        font-size: 32px;
        line-height: 35px;
    }
    p.sub-title {
        margin: 0 auto 40px;
        font-size: 16px;
        line-height: 22px;
    }
}

@media screen and (max-width: 415px) {
    h3.title-main {
        font-size: 30px;
        line-height: 32px;
    }
    p.sub-title {
        margin: 0 auto 30px;
    }
    .buttonbg {
        font-size: 16px;
        padding: 11px 16px;
    }
}

