.w3l-headers-9 .side-menu-hny label.menuclose {
    text-transform: uppercase;
    text-align: center;
}

.w3l-headers-9 .side-menu-hny label.menuclose:hover {
    cursor: pointer;
}

.w3l-headers-9 .overlay-menuv {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.9);
    z-index: 999;
}

.w3l-headers-9 .overlay-menuv label.menuclose {
    width: 28px;
    height: 28px;
    position: absolute;
    right: 20px;
    top: 70px;
    background: url(./images/cross.png) no-repeat;
    z-index: 100;
    cursor: pointer;
    z-index: 999;
}

.w3l-headers-9 .overlay-menuv nav {
    text-align: center;
    position: relative;
    top: 50%;
    font-size: 54px;
    transform: translateY(-50%);
}

.w3l-headers-9 .overlay-menuv ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: inline-block;
    position: relative;
}

.w3l-headers-9 .overlay-menuv ul li {
    display: block;
    margin: 20px 0;
    text-align: left;
}

.w3l-headers-9 .overlay-menuv ul li a {
    display: block;
    color: white;
    text-decoration: none;
    transition: color 0.2s;
    font-size: 17px;
    font-weight: normal;
}

.w3l-headers-9 .side-menu-hny~.overlay-menuv-hugeinc {
    opacity: 0;
    visibility: hidden;
}

.w3l-headers-9 #op:checked~.overlay-menuv-hugeinc {
    opacity: 1;
    visibility: visible;
}

.w3l-headers-9 .overlay-menuv-hugeinc nav {
    perspective: 300px;
    max-width: 600px;
    margin: 0 auto;
}

.w3l-headers-9 .overlay-menuv-hugeinc nav ul {
    opacity: 0.4;
}

.w3l-headers-9 #op:checked~.overlay-menuv-hugeinc nav ul {
    opacity: 1;
    transform: rotateX(0deg);
}

.w3l-headers-9 #op:not(:checked)~.overlay-menuv-hugeinc nav ul {
    transform: translateY(25%) rotateX(-35deg);
}

.w3l-headers-9 form.search-head-form input {
    display: block;
    background: #fff;
    border: none;
    padding: 14px;
    font-size: 15px;
    color: #8298AB;
    width: 100%;
}

.w3l-headers-9 h6.search-text {
    color: #fff;
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 1em;
}

.w3l-headers-9 .buttonbg {
    padding: 13px 40px;
}

.w3l-headers-9 .toggle-2,
.w3l-headers-9 .toggle,
.w3l-headers-9 [id^=drop] {
    display: none;
}

.w3l-headers-9 #logo a img {
    height: 42px;
}

.w3l-headers-9 nav ul {
    position: relative;
}

.w3l-headers-9 nav ul li {
    display: inline-block;
    position: relative;
    padding: 0 12px;
    vertical-align: middle;
}

.w3l-headers-9 nav ul li ul li {
    display: inherit;
    padding: 0;
}

.w3l-headers-9 input#nav {
    display: none;
}

.w3l-headers-9 ul.menu a.link-nav {
    color: #2C3038;
    font-weight: normal;
    font-size: 17px;
    line-height: 60px;
}

.w3l-headers-9 ul.menu ul:after {
    position: absolute;
    top: -9px;
    left: 0;
    right: 0;
    margin: 0 auto;
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    background: #fff;
    border-radius: 3px 0 0 0;
    transform: rotate(45deg);
    border-top: 1px solid #EAEDF2;
    border-left: 1px solid #EAEDF2;
}

.w3l-headers-9 nav ul ul {
    display: none;
    background: #fff;
    border-radius: 4px;
    border: 1px solid #EAEDF2;
    padding: 1rem;
    min-width: 180px;
    position: absolute;
    top: 55px;
    left: 0;
    box-shadow: 0 0 1px #eee, 0 6px 18px rgba(0, 0, 0, 0.15);
    transform: translateX(-60px);
    z-index: 9;
}

.w3l-headers-9 ul.first-dropdwon {
    min-width: 530px;
    -webkit-columns: 3;
    -moz-columns: 3;
    columns: 3;
    transform: translateX(-230px);
}

.w3l-headers-9 ul.menu ul li a {
    color: #666;
    font-size: 16px;
    padding: 4px;
    display: block;
}

.w3l-headers-9 nav ul li:hover>ul {
    display: inherit;
}

.w3l-headers-9 .nav_w3ls nav ul ul li {
    width: 170px;
    float: none;
    display: block;
    position: relative;
    margin: 0;
}

.w3l-headers-9 .nav_w3ls nav ul ul li a {
    display: block;
    color: #333;
    padding: 4px 8px;
    font-size: 16px;
}

.w3l-headers-9 .nav_w3ls nav ul ul li a:hover {
    color: #000;
    background: #eee;
    opacity: 1;
}

.w3l-headers-9 nav ul ul ul li {
    position: relative;
    top: -60px;
    left: 170px;
}

